import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import Calendar from 'react-calendar';

import css from './calendar.module.css'; //  CSS 

const TaskCalendar = () => {
  const tasks = useSelector((state) => state.tasks.tasks); //    Redux 
  const [value, setValue] = useState(new Date());
  const [isOpen, setIsOpen] = useState(false); //      
  const [selectedDate, setSelectedDate] = useState(null); //     

  const taskDates = tasks.map(task => new Date(task.endTime).toDateString());

  //       
  useEffect(() => {
    if (Notification.permission === 'default') {
      Notification.requestPermission().then(permission => {
        if (permission !== 'granted') {
          console.warn('  ');
        }
      });
    }
  }, []);

  //   
  const showNotification = (task) => {
    if (Notification.permission === 'granted') {
      new Notification(` !`, {
        body: `: ${task.taskName}\n: ${new Date(task.endTime).toLocaleString()}`,
        icon: '/path/to/icon.png', //     
      });
    }
  };

  //     
  useEffect(() => {
    const checkDeadlines = () => {
      const now = new Date();
      tasks.forEach(task => {
        const taskDeadline = new Date(task.endTime);
        const timeDifference = taskDeadline - now; //   
        const oneDayInMs = 24 * 60 * 60 * 1000;

        if (timeDifference > 0 && timeDifference <= oneDayInMs) {
          showNotification(task); //     
        }
      });
    };

    const intervalId = setInterval(checkDeadlines, 60 * 1000); //   

    return () => clearInterval(intervalId); //    
  }, [tasks]);

  const tileClassName = ({ date, view }) => {
    if (view === 'month') {
      const dateStr = date.toDateString();
      if (taskDates.includes(dateStr)) {
        return `${css.highlightTask} ${css.reactCalendar__tile__hasTask}`; //       
      }
      if (dateStr === new Date().toDateString()) {
        return css.reactCalendar__tileNow; //     
      }
    }
  };

  const formatMonthYear = (locale, date) => {
    const monthYearString = date.toLocaleString(locale, { month: 'long', year: 'numeric' });
    return (
      <div className={css.customMonthYear}>
        <span>{monthYearString}</span>
      </div>
    );
  };

  const handleDateClick = (date) => {
    setSelectedDate(date.toDateString());
    setValue(date); // Update value to selected date
    setIsOpen(!isOpen); // Toggle visibility of task list on date click
  };



  const filteredTasks = tasks.filter(task => new Date(task.endTime).toDateString() === selectedDate);

  return (
    <div className={css.my_calendar}>
      <Calendar
        onChange={setValue}
        value={value}
        tileClassName={tileClassName}
        className={`my_calendar ${css.reactcalendar}`}
        formatMonthYear={formatMonthYear} //    
        next2Label={null} //     
        prev2Label={null} //     
        onClickDay={handleDateClick} //      
      />
      
      {isOpen && (
        <>
        <p className={css.task_you}> </p>
        <ul className={css.taskList}>
          {filteredTasks.map(task => (
           <li key={task._id} className={`${css.taskItem} ${task.done ? css.completed : ''}`}>
             {task.done && <div className={css.taskCompletedIcon}></div>}
             <div>
               <h3 className={task.done ? css.doneTask : ''}>{task.taskName}</h3>
             </div>
           </li>
          ))}
        </ul>
        
        
        </>
      )}
    </div>
  );
};

export default TaskCalendar;
