import React, { useState } from "react";
import { useDispatch } from "react-redux";
import 'react-toastify/dist/ReactToastify.css';
import { fetchRegistrThunk } from "../../redux/Auth/auth.thunk";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import css from "./LoginForm.module.css";
import { Link } from "react-router-dom"

export const LoginForm = () => {
  const [formData, setFormData] = useState({
    email: '',
    password: ''
  });

  const dispatch = useDispatch();
  const navigate = useNavigate();

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData((prevState) => ({ ...prevState, [name]: value }));
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    try {
      //  Thunk-  
      const result = await dispatch(fetchRegistrThunk(formData)); //   .unwrap()
      console.log(result); //    
  
      //     (fulfilled)
      if (result.type === "auth/login/fulfilled" && result.payload.token) {
        toast.success(" !");
        navigate('/', { replace: true }); //     
      } else {
        //      ,  
        toast.error(result.payload?.message || " !");
      }
    } catch (error) {
      //      ,  
      
      toast.error(error.message || "Error occurred!");
    }
  };

  return (
    
      <form className={css.form} onSubmit={handleSubmit} autoComplete="off">
      
        
        <label className={css.label}>
          <span className={css.span}>  </span>
          <input
            className={css.input}
            type="email"
            name="email"
            onChange={handleChange}
            value={formData.email}
            required
          />
        </label>
        
        <label className={css.label}>
          <span className={css.span}></span>
          <input
            className={css.input}
            type="password"
            name="password"
            onChange={handleChange}
            value={formData.password}
            required
          />
        </label>

        

        <button className={css.button} type="submit"></button>

        <div className={css.footer}>
          ?<Link className={css.link} to="/join"></Link>
        </div>
      </form>
    
  );
};
