import { createAsyncThunk } from "@reduxjs/toolkit";
import {privateApi} from "../../components/API/API"; //   API-


export const fetchTasksThunk = createAsyncThunk(
  "tasks/fetch",
  async (_, { rejectWithValue, getState }) => {
    try {
      //    
      const state = getState();
      const token = state.auth?.token; // ,    state.auth.token

      //    API
      const { data } = await privateApi.get("/task", {
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
        },
        withCredentials: true,
      });

      return data; //   
    } catch (error) {
      return rejectWithValue(error.response?.data || "    ");
    }
  }
);

//   id 


export const addTaskThunk = createAsyncThunk(
  "task",
  async (taskData, { rejectWithValue, getState }) => {
    try {
      //    
      const state = getState();
      const token = state.auth?.token; // ,    state.auth.token

      const { data } = await privateApi.post(
        "/task",
        taskData,
        {
          headers: {
            Authorization: `Bearer ${token}`, //    
          },
          withCredentials: true,
        }
      );
      return data;
    } catch (error) {
      return rejectWithValue(error.response?.data || "    ");
    }
  }
);


export const updateTaskThunk = createAsyncThunk(
  'task/update',
  async ({ taskId, updates }, { rejectWithValue, getState }) => {
    try {
      const state = getState();
      const token = state.auth?.token;

      const { data } = await privateApi.put(
        `task/${taskId}`,
        updates,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      console.log(data); //   
      return data; //   ,     
    } catch (error) {
      return rejectWithValue(error.response?.data || '    ');
    }
  }
);

export const deleteTaskThunk = createAsyncThunk(
  'task/delete',
  async (id, { rejectWithValue, getState }) => {
    try {
      //    
      const state = getState();
      const token = state.auth?.token; // ,     state.auth.token

      await privateApi.delete(`/task/${id}`, {
        headers: {
          Authorization: `Bearer ${token}`, //    
        },
        withCredentials: true,
      });

      return id; //  ID  
    } catch (error) {
      return rejectWithValue(error.response?.data || '    ');
    }
  }
);


